// ===============================================================
// FONTS
// ===============================================================

@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');

// ===============================================================
// SASS VARIABLES
// ===============================================================

// Site wide custom breakpoints for use with
// https://eduardoboucas.github.io/include-media/
/* prettier-ignore */
$breakpoints: (
	phone: (650 / 16) * 1em,
	tablet: (800 / 16) * 1em,
	desktop: (1000 / 16) * 1em,
	desktopLarge: (1250 / 16) * 1em,
	desktopXLarge: (1600 / 16) * 1em,
	desktopXXLarge: (1850 / 16) * 1em,
);

// Special use custom breakpoints, see link for examples
// https://eduardoboucas.github.io/include-media/#documentation
$phone-portrait: "<650px", "portrait";
$phone-landscape: "<800px", "landscape";
$tablet-portrait: ">650px", "<1400px", "portrait";

// Colors generic
$color-white: #fff;
$color-black: #191919;
$color-grey-light: #f2f2f2;
$color-grey-medium: #4d4d4d;
$color-red: #d63e2d;

// Colors brands
$color-primary: $color-red;

// Colours for interactive map
$color-map-red: #C43021;
$color-map-orange: #ef6e3a;
$color-map-blue: #2F52A0;
$color-map-yellow: #EEE754;
$color-map-green: #8DBF35;
$color-map-grey: #464646;

// Fonts
$font-primary: "Poppins", "Helvetica Neue", Helvetica, Arial, sans-serif;

// Easing
$easing-default: cubic-bezier(0.215, 0.61, 0.355, 1);

// Sizes
$map-sidebar-width: 360px;

