// ===============================================================
// MIXINS AND FUNCTIONS
// ===============================================================

@mixin transition($styles: all,$time: 0.4s, $easing: $easing-default) {
	transition: $styles $time $easing;
}

@mixin removeChildMargins {
	& > *:first-child { margin-top: 0; }
	& > *:last-child { margin-bottom: 0; }
}

@mixin absoluteFill {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}