.interactive-map {
  position: relative;
  overflow: hidden;
  display: flex;  
  flex-direction: column;

  font-family: 'lato', Helvetica, Arial, sans-serif;

  h1, h2, h3, h4, h5, p {
    font-family: 'lato', Helvetica, Arial, sans-serif;
  }

  @media only screen and (min-width: 800px) {
    flex-direction: row;
  }

  .map-container {
    position: relative;
    flex: 1;

    @media only screen and (min-width: 800px) {
      margin-right: $map-sidebar-width;
    }
  }

  .background-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-size: cover;
    background-position: center;
    opacity: 0.8;
  }

  .map-annotations {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 4;
    pointer-events: none;

    .map-annotation {
      position: absolute;
      color: $color-white;
      font-size: 16px;
      font-weight: bold;
      text-shadow: 1px 1px 1px rgba(0,0,0,0.5);

      &:before {
        content: '';
        position: absolute;
        top: calc(100% + 5px);
        left: 50%;
        transform: translateX(-50%);
        width: 1px;
        height: 30px;
        border-right: 1px dashed white;
      }

      &.line-up {
        &:before {
          top: auto;
          bottom: calc(100% + 5px);
        }
      }
    }
  }

  .map-svg {
    position: relative;
    z-index: 2;

    g {
      cursor: pointer;
    }

    .district-boundary,
    .desilting-work-finished,
    .desilting-work-undertaken,
    .middle-level-drains {
      display: inline;
      fill: none;
      stroke-width: 4;
      stroke-miterlimit: 10;
      @include transition(all, 1s);
    }

    .district-boundary          { stroke: $color-map-red; }
    .desilting-work-finished    { stroke: $color-map-yellow; }
    .desilting-work-undertaken  { stroke: $color-map-green; }
    .middle-level-drains        { stroke: $color-map-blue; }
    .pumping-station            { fill: $color-map-red; }
    .lock                       { fill: $color-map-orange; }
  }

  .map-key {
    position: relative;
    z-index: 3;
    background: #f9f9f9;
    box-shadow: 0px 0px 40px rgba(0,0,0,0.5);

    @media only screen and (min-width: 800px) {
      width: $map-sidebar-width;
      position: absolute;
      right: 0;
      height: 100%;
    }
  
    .map-key-header {
      padding: 15px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: $color-primary;
      color: $color-white;

      & > * { margin: 0; }

      .clear-selection {
        font-size: 14px;
        color: $color-white;
        text-decoration: none;
      }
    }

    .map-key-items {
      display: flex;
      flex-direction: column;
      height: calc(100% - 52px);
      overflow-y: scroll;
      
      .map-key-item {
        border-bottom: 1px solid rgba(0,0,0,0.1);

        &.active {
          background: $color-white;
          .map-key-item-link-container {
            background: $color-white !important;
            font-weight: bold;
          }
          .map-key-item-content {
            display: block;
          }
        }

        &:hover,
        &:focus {
          opacity: 1 !important;
        }

        .map-key-item-link-container {
          position: relative;
          padding: 10px 15px;
          min-height: 27px;
          display: flex;
          flex-direction: row;
          align-items: center;
          gap: 20px;
          transition: background 420ms cubic-bezier(0.215, 0.61, 0.355, 1);
          font-size: 20px;
          line-height: 24px;

          &:hover,
          &:focus {
            background: rgba(0,0,0,0.05);
          }

        }

        .map-key-item-content {
          display: none;
          padding: 0px 15px;

          .image {
            display: block;
            background: rgba(0,0,0,0.1);
            background-size: cover;
            background-position: center;
            padding-bottom: 45%;
          }

          .video-cover {
            position: relative;
            cursor: pointer;

            &:hover {
              .video-play {
                background: $color-white;

                svg {
                  path {
                    fill: $color-primary;
                  }
                }
              }

              &:before {
                opacity: 0.8;
              }
            }
  
            &:before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background: $color-black;
              opacity: 0.3;
              @include transition();
            }
            
            .video-play {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%,-50%);
              width: 70px;
              height: 70px;
              border-radius: 50%;
              background: $color-primary;
              @include transition();
              
              svg {
                width: 36px;
                height: auto;
                position: absolute;
                top: 50%;
                left: 54%;
                transform: translate(-50%,-50%);

                path {
                  fill: $color-white;
                }
              }
            }
          }

          .events {
            padding-bottom: 10px;

            & > h4 {
              position: relative;
              color: white;
              display: inline-block;
              background: $color-primary;
              padding: 6px 15px 6px 20px;
              margin: 0px 0px 10px 0px;
              position: relative;
              left: -15px;
              line-height: 20px !important;

              &:after {
                content: '';  
                position: absolute;
                left: 100%;
                top: 0;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 32px 15px 0 0;
                border-color: $color-primary transparent transparent transparent;
              }
            }
          
            .event {
              display: flex;
              flex-direction: row;
              gap: 0px 20px;
              font-size: 13px;
              line-height: 18px;
              padding: 10px 0px;
              border-top: 1px solid rgba(0,0,0,0.1);
              @include transition;

              &:first-of-type {
                border-top: none;
              }
              
              .event-date {
                font-size: 11px;
                line-height: 20px;
                color: #989898;
                min-width: 50px;
                max-width: 50px;
                @include transition;
              }
          
              .event-title {
                font-weight: bold;
                padding-right: 20px;

                a {
                  color: $color-black;
                  text-decoration: none;
              
                  &:hover,
                  &:focus {
                    color: $color-primary;
                    text-decoration: underline;
                  }
                }
              }
          
            }
          }

          p {
            font-size: 14px;
            line-height: 18px;
          }
        }

        &[data-key-item="middle-level-drains"] {
          .map-key-icon .icon-line { background: $color-map-blue; }
        }

        &[data-key-item="desilting-work-undertaken"] {
          .map-key-icon .icon-line { background: $color-map-green; }
        }

        &[data-key-item="desilting-work-finished"] {
          .map-key-icon .icon-line { background: $color-map-yellow; }
        }

        &[data-key-item="pumping-station"] {
          .map-key-icon .icon-circle { background: $color-map-red; }
        }

        &[data-key-item="lock"] {
          .map-key-icon .icon-svg svg polygon { fill: $color-map-orange; }
        }
        
      }

      .map-key-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        max-width: 30px;
        min-width: 30px;

        & > {
          display: block;
        }

        .icon-line {
          height: 5px;
          width: 100%;
        }

        .icon-circle {
          width: 20px;
          height: 20px;
          border-radius: 50%;
        }

        .icon-svg {
          width: 20px;
          svg {
            display: block;
            width: 100%;
          }
        }

        .icon-boundary {
          width: 20px;
          height: 20px;
          border: 4px solid $color-map-red;
        }
      }

      .map-key-text {
        font-size: 0.9em;
        line-height: 1.5em;
      }

      .key-item-link {
        @include absoluteFill;
      }
    }

    a.clear-selection {
      display: none;
    }
  }

  &.key-item-selected {
    svg {
      g:not(.active) {
        polyline, line { stroke: $color-map-grey; }
        polygon, circle { fill: $color-map-grey; }
      }
    }

    .map-key {
      a.clear-selection {
        display: inline-block;
      }

      .map-key-items {
        .map-key-item:not(.active) {
          opacity: 0.5;
        }
      }
    }
  }
}